@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
body {
  font-family: "Roboto", sans-serif;
}

/* Rating display container */
.rating-display {
  display: flex;
  flex-wrap: wrap; /* Allow wrapping for smaller screens */
  justify-content: center;
  gap: 10px;
}

/* Desktop View: Arrange all items horizontally */
.rating-display.desktop-view {
  flex-wrap: nowrap;
  justify-content: space-around;
}

/* Mobile View: Arrange items vertically */
.rating-display.mobile-view {
  flex-direction: column; /* Stack the items vertically */
  justify-content: center; /* Center them vertically */
  gap: 20px; /* Adjust space between items */
  flex-wrap: nowrap; /* Prevent wrapping of items */
  overflow-x: hidden; /* Hide overflow horizontally */
}

/* Each rating item */
.rating-item {
  font-size: 18px;
  padding: 10px;
  text-align: center;
}

/* Emoji specific styles */
.rating-emoji {
  width: 40px;
  height: 40px;
  object-fit: cover;
}

/* Mobile adjustments for ratings */
@media (max-width: 576px) {
  /* Mobile layout: Stack items vertically */
  .rating-display.mobile-view {
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    flex-wrap: nowrap;
    overflow-x: hidden;
    padding-top: 48px;
  }
  .surveypreviewview {
    justify-content: flex-start;
  }
  /* Smaller text and font sizes for mobile */
  .rating-item {
    font-size: 16px; /* Adjust font size for mobile */
  }

  .rating-emoji {
    width: 30px; /* Smaller emoji size for mobile */
    height: 30px; /* Smaller emoji size for mobile */
  }

  .rating-item-text {
    font-size: 14px; /* Smaller text for rating */
  }

  .previewbody {
    padding: 20px 0px !important;
    text-align: center !important;
  }
  .addsurvey-container {
    height: auto; /* Allow auto height for small screens */
    /* Reduce padding */
    font-size: 16px; /* Smaller font size */
    flex-direction: column;
    padding-top: 48px;
  }
}

/* Medium devices (tablets, 577px to 768px) */
@media (min-width: 577px) and (max-width: 768px) {
  .rating-display.desktop-view {
    flex-wrap: nowrap;
    justify-content: space-between; /* Space items evenly */
  }

  /* Font size adjustments for tablet screens */
  .rating-item {
    font-size: 18px; /* Adjust font size for tablets */
  }

  .rating-emoji {
    width: 35px; /* Slightly larger emoji size for tablet */
    height: 35px; /* Slightly larger emoji size for tablet */
  }

  .rating-item-text {
    font-size: 15px; /* Adjust text size for tablet */
  }
  .addsurvey-container {
    height: "100vh";
    padding: "20px";
    font-family: "Arial, sans-serif";
    background-color: "#f9fafb";
    padding-top: 48px;
  }
  .surveypreviewview {
    justify-content: flex-start;
  }
  .previewbody {
    padding: 20px 0px !important;
    text-align: center !important;
  }
  .addsurvey-container {
    height: auto;
    padding: 20px;
    font-size: 20px; /* Slightly larger font */
    flex-direction: column; /* Still stack vertically */
  }
}

/* Large devices (desktops, 769px to 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .rating-display.desktop-view {
    flex-wrap: nowrap;
    justify-content: space-around; /* Distribute items evenly */
  }

  /* Font sizes for large tablets and small desktops */
  .rating-item {
    font-size: 20px; /* Slightly larger font for large screens */
  }

  .rating-emoji {
    width: 45px; /* Larger emoji size */
    height: 45px; /* Larger emoji size */
  }

  .rating-item-text {
    font-size: 16px; /* Slightly larger text */
  }
  .addsurvey-container {
    color: #0a2472;
    font-family: Roboto;
    font-size: 36px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
  .addsurvey-container {
    flex-direction: row; /* Switch to horizontal layout */
    justify-content: space-between; /* Space out items */
    padding: 30px;
    font-size: 24px; /* Larger font size */
    line-height: 1.8; /* Increase line spacing */
  }
  .previewbody {
    padding: 20px !important;
    text-align: center !important;
  }
  .surveypreviewview {
    justify-content: flex-end;
  }
}

/* Extra-large devices (large desktops, 1025px and above) */
@media (min-width: 1025px) {
  .rating-display.desktop-view {
    flex-wrap: nowrap;
    justify-content: space-around; /* Evenly distribute items */
  }
  .surveypreviewview {
    justify-content: flex-end;
  }

  /* Adjust the font sizes for larger screens */
  .rating-item {
    font-size: 22px; /* Larger font for large desktops */
  }

  .rating-emoji {
    width: 50px; /* Larger emoji size */
    height: 50px; /* Larger emoji size */
  }

  .rating-item-text {
    font-size: 18px; /* Larger text for large desktops */
  }

  .previewbody {
    padding: 20px !important;
    text-align: center !important;
  }
  .addsurvey-container {
    flex-direction: row; /* Maintain horizontal layout */
    justify-content: space-around; /* Distribute items evenly */
    padding: 40px;
    font-size: 30px; /* Largest font size */
    line-height: 2; /* Extra line spacing */
  }
}

.custom-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3;
  border-top: 5px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.addsurvey-container {
  display: flex;
  height: "100vh";
  padding: "20px";
  font-family: "Arial, sans-serif";
  background-color: "#f9fafb";
  color: #0a2472;
  font-family: Roboto;
  font-size: 36px !important;
  font-style: normal;
  font-weight: 400 !important;
  line-height: normal;
}
.surveyLabel {
  color: #0a2472;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}
select {
  position: relative;
  overflow: hidden;
  z-index: 1000;
}

select:focus {
  outline: none;
}

select option {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.addsurvey-container select {
  max-width: 100%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 1000;
}

.addsurvey-container {
  position: relative;
  overflow: visible; /* Ensure the container doesn't clip child elements */
}
