/* General Sidebar Styles */
.sidebar {
  background-color: #ebf3fc;
  width: 250px;
  height: 100vh;
  position: fixed;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all 0.3s ease;
  z-index: 1000;
}

.sidebar.collapsed {
  width: 60px; /* Reduced width when collapsed */
}

.sidebar.collapsed .nav-text {
  display: none;
}

.sidebar .icon {
  width: 25px; /* Default icon size */
  height: 25px;
  transition: width 0.3s, height 0.3s; /* Smooth transition for resizing */
}

.sidebar.collapsed .icon,
.sidebar.collapsed .img-fluid {
  width: 25px; /* Larger icon size when collapsed */
  height: 25px;
}

.sidebar.collapsed .img-fluid {
  width: 40px; /* Larger logo size when collapsed */
  height: 40px;
}

.sidebar .nav-text {
  margin-left: 10px;
}

.sidebar-content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.sidebar-content.open {
  display: block;
}

.hamburger-menu {
  display: none;
  font-size: 1.5rem;
  padding: 0.5rem;
  cursor: pointer;
}

.logout-btn {
  color: black;
  background: none;
  border: none;
  cursor: pointer;
  text-decoration: underline;
}

.sidebar-bottom {
  margin-top: auto;
}

.nav-item {
  justify-content: space-between;
  align-items: center;
}

.nav-link {
  display: flex;
  align-items: center;
  padding: 10px 20px;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .sidebar {
    width: 60px; /* Collapsed state on mobile */
    position: absolute;
    height: auto;
  }

  .hamburger-menu {
    display: block; /* Show hamburger menu on mobile */
    position: fixed;
    top: 10px;
    left: 10px;
  }

  .sidebar-content {
    display: none;
  }

  .sidebar-content.open {
    display: flex;
    flex-direction: column;
  }

  .nav-text {
    display: none; /* Hide text on smaller screens */
  }

  .nav-link {
    text-align: center;
  }

  .nav-link img {
    margin: 0;
  }

  .sidebar.collapsed .icon {
    width: 30px; /* Larger icon size for collapsed state on mobile */
    height: 30px;
  }

  .desktop-only {
    display: none; /* Hide on small screens */
  }

  .mobile-only {
    display: block; /* Show on small screens */
    position: fixed;
    top: 10px;
    right: 10px; /* Float right on small screens */
    z-index: 1000;
    margin: 0;
    background: #ebf3fc;
    padding: 5px;
    border-radius: 5px;
  }

  .mobile-only img {
    max-width: 70px; /* Increased size for better visibility */
    height: auto;
  }
}

@media (min-width: 769px) {
  .mobile-only {
    display: none; /* Hide the mobile logo on larger screens */
  }

  .desktop-only {
    display: block;
    text-align: center;
    padding: 10px;
  }
}

/* Sidebar Container and Main Content Behavior */
.sidebar-container.collapsed .sidebar-content {
  transform: translateX(-250px); /* Adjust based on your sidebar width */
}

.main-content {
  transition: margin-left 0.3s ease;
}

.sidebar-container.collapsed .main-content {
  margin-left: 250px; /* Adjust based on your sidebar width */
}
