html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden; /* Prevent scrolling */
}
/* Adaptive Container */
.adaptive-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  background-color: #f5f5f5;
}

/* Adaptive Inner Container */
.adaptive-inner-container {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 500px;
}

/* Adaptive Input Field */
.adaptive-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

/* Adaptive Buttons */
.adaptive-button {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  border-radius: 5px;

  color: white;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
}

/* Adaptive Field */
.adaptive-field {
  width: 100%;
  margin-bottom: 15px;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .adaptive-inner-container {
    width: 90%;
  }
}

@media (max-width: 992px) {
  .adaptive-inner-container {
    padding: 15px;
  }
}

@media (max-width: 768px) {
  .adaptive-container {
    padding: 15px;
  }

  .adaptive-inner-container {
    width: 100%;
    padding: 15px;
  }

  .adaptive-button {
    font-size: 14px;
    padding: 8px;
  }
}

@media (max-width: 576px) {
  .adaptive-container {
    padding: 10px;
  }

  .adaptive-inner-container {
    padding: 10px;
  }

  .adaptive-button {
    font-size: 14px;
    padding: 6px;
  }
}
.full-screen-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(
    0,
    0,
    0,
    0.5
  ); /* Optional: to add semi-transparent background */
  z-index: 9999; /* Ensure it's on top of other elements */
}

/* Spinner image styling */
.spinner {
  width: 50px; /* Customize the size */
  height: 50px; /* Customize the size */
  object-fit: contain; /* Ensure the GIF fits its container */
}
