.table-custom {
  border-collapse: collapse;
}

.table-custom th,
.table-custom td {
  border-left: none; /* Remove left border */
  border-right: none; /* Remove right border */
}

.table-custom tr {
  border-bottom: 1px solid #dee2e6; /* Add bottom border to rows */
}

