/* General table styles */
.surveyTable {
  border-collapse: collapse;
  width: 100%;
}

.surveyTable th,
.surveyTable td {
  padding: 10px;
  border: none;
}

.surveyTable th {
  background-color: #9ee2ff;
  text-align: left;
}

.surveyTable td {
  text-align: left;
  vertical-align: middle;
}

/* Name column */
.surveyTable .survey-name-column {
  padding-left: 200px;
  width: 50%;
}

/* Actions column */
.surveyTable .actions-column {
  /* padding-left: 250px; */
  width: 50%;
  text-align: center;
}

/* Red warning text for surveys in progress */
.surveyTable .survey-warning {
  color: red;
}

/* Edit and Delete buttons */
.surveyTable .btn-edit,
.surveyTable .btn-delete {
  display: inline-flex;
  align-items: center;
  padding-left: 20px;
}

.surveyTable .btn-edit img,
.surveyTable .btn-delete img {
  width: 20px;
  height: 20px;
}

/* Disabled button state */
.surveyTable .btn-edit[disabled] {
  pointer-events: none;
}

