/* ==============================
   Responsive Media Queries
   ============================== */

/* Small devices (max-width: 576px) */
@media (max-width: 576px) {
  .surveyHeader {
    font-size: 24px;
  }

  #survey-dropdown {
    width: 100%;
  }

  .download-text {
    font-size: 18px;
  }

  .btn-download {
    font-size: 14px;
  }

  .surveyTable th,
  .surveyTable td {
    font-size: 12px;
    padding: 8px; /* Adjust padding for small screens */
  }

  .labeldesign {
    font-size: 13px;
  }

  .sendMail {
    font-size: 20px;
  }

  .recepient {
    font-size: 18px;
  }

  .surveyTable .survey-name-column {
    padding-left: 10px; /* Reduce padding on small screens */
  }
}

/* Medium devices (tablets, 577px to 768px) */
@media (max-width: 768px) {
  .surveyHeader {
    font-size: 28px;
  }

  #survey-dropdown {
    width: 60%;
  }

  .download-text {
    font-size: 20px;
  }

  .btn-download {
    font-size: 16px;
  }

  .surveyTable th,
  .surveyTable td {
    font-size: 14px;
  }

  .labeldesign {
    font-size: 14px;
  }

  .sendMail {
    font-size: 22px;
  }

  .recepient {
    font-size: 19px;
  }

  .surveyTable .survey-name-column {
    padding-left: 20px; /* Adjust padding */
  }
}

/* Large devices (desktops, 769px to 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .surveyHeader {
    font-size: 32px;
  }

  #survey-dropdown {
    width: 50%;
  }

  .download-text {
    font-size: 22px;
  }

  .btn-download {
    font-size: 14px;
  }

  .surveyTable th,
  .surveyTable td {
    font-size: 15px;
  }

  .labeldesign {
    font-size: 15px;
  }

  .sendMail {
    font-size: 24px;
  }

  .recepient {
    font-size: 20px;
  }

  .surveyTable .survey-name-column {
    padding-left: 20px; /* Adjust padding */
    font-family: "Poppins", sans-serif;
  }
  .surveyTable .survey-column {
    padding-left: 20px; /* Adjust padding */
    font-family: "Poppins", sans-serif;
  }
}

/* Extra-large devices (large desktops, 1025px and above) */
@media (min-width: 1025px) {
  .surveyHeader {
    font-size: 36px;
  }

  #survey-dropdown {
    width: 40%;
  }

  .download-text {
    font-size: 25px;
  }

  .btn-download {
    font-size: 20px;
  }

  .surveyTable th,
  .surveyTable td {
    font-size: 15px;
  }

  .labeldesign {
    font-size: 16px;
  }

  .sendMail {
    font-size: 26px;
  }

  .recepient {
    font-size: 21px;
  }

  .surveyTable .survey-name-column {
    padding-left: 200px; /* Adjust padding */
    font-family: "Poppins", sans-serif;
  }
  .surveyTable .survey-column {
    padding-left: 200px; /* Adjust padding */
    font-family: "Poppins", sans-serif;
  }
}
.upload-box {
