/* Grouping Buttons for Actions */
.action-btn-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
}

/* Button Styling */
.custom-btn {
  padding: 5px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.custom-btn:hover {
  transform: scale(1.1);
  background-color: #f0f0f0;
}

/* Icon Styling */
.action-icon {
  width: 20px;
  height: 20px;
}

@media (max-width: 576px) {
  .action-icon {
    width: 16px;
    height: 16px;
  }
  .custom-btn {
    padding: 4px;
  }
}
/* Action Button Styling */
.unique-action-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 6px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease, background-color 0.3s ease;
}

.unique-action-button:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.05);
}

/* Edit Action Specific Styling */
.unique-edit-action:hover {
  background-color: rgba(0, 123, 255, 0.1); /* Subtle blue tint */
}

/* Delete Action Specific Styling */
.unique-delete-action:hover {
  background-color: rgba(255, 0, 0, 0.1); /* Subtle red tint */
}

/* Icon Styling */
.unique-action-icon {
  width: 22px;
  height: 22px;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.unique-action-icon:hover {
  transform: rotate(15deg);
}

/* Responsive Styling for Smaller Screens */
@media (max-width: 576px) {
  .unique-action-icon {
    width: 18px;
    height: 18px;
  }

  .unique-action-button {
    padding: 5px;
  }
}
/* Modal Box Styles */
.modal-content {
  max-width: 800px; /* Default max-width */

  margin: 0 auto; /* Center the modal */
  border-radius: 8px; /* Rounded corners */
}

/* Modal Header */
.modal-header {
  background-color: #0a2472;
  color: white;
  justify-content: center;
}

.modal-title {
  text-align: center;
  width: 100%;
}

/* Modal Body */
.modal-body {
  padding: 20px;
  overflow-y: auto;
}

/* Modal Footer */
.modal-footer {
  display: flex;
  justify-content: center;
  width: 100%;
}

/* Button Styles in Footer */
.modal-footer .btn {
  width: 100%;
  max-width: 300px; /* Limit button width for large screens */
  margin-bottom: 10px;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .modal-content {
    width: 95%; /* Take up more width on smaller screens */
    max-width: 95%; /* Ensure the modal does not overflow */
  }

  .modal-header {
    padding: 15px;
  }

  .modal-title {
    font-size: 1.2rem; /* Adjust font size for smaller screens */
  }

  .modal-body {
    padding: 15px; /* Reduce padding for mobile */
  }

  .modal-footer .btn {
    width: 100%; /* Ensure buttons stretch full width */
    max-width: 100%; /* Full width for mobile buttons */
    padding: 10px; /* Make buttons more touch-friendly */
  }
}

@media (max-width: 576px) {
  .modal-content {
    width: 95%; /* Slightly more space on very small screens */
  }

  .modal-header {
    padding: 10px; /* Adjust header padding */
  }

  .modal-title {
    font-size: 1rem; /* Smaller title on very small screens */
  }

  .modal-body {
    padding: 10px; /* Reduce padding for mobile */
  }

  .modal-footer .btn {
    padding: 12px; /* Larger buttons for mobile */
  }
}
.custom-spinner {
  width: 50px;
